const gallery = document.querySelector('.gallery');

let picturesMarkup = '';

for (const image of images) {
  const { preview, original, description } = image;
  const picture = `<li class="gallery-item">
  <a class="gallery-link" href="${original}">
    <img
      class="gallery-image"
      src="${preview}"
      alt="${description}"
    />
  </a>
</li>`;
  picturesMarkup += picture;
}

gallery.insertAdjacentHTML('afterbegin', picturesMarkup);

const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
  captionPosition: 'bottom',
});


